import React from "react";
import { Container, Navbar } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { HiBars3 } from "react-icons/hi2";

const NavigationBar = () => {
  const [isClicked, setIsClicked] = React.useState(false);

  return (
    <Header>
      <Navbar
        style={{ background: "rgba(19, 15, 64,1.0)" }}
        variant="dark"
        fixed="top"
      >
        <Container fluid style={{ position: "relative" }}>
          <Link to="/">
            <Brand>
              <Image
                src={require("../components/images/BOU logo.png")}
                alt="logo"
              />
              <BrandPanel>
                <BrandText>  </BrandText>
                <BrandText>Bangladesh Open University</BrandText>
              </BrandPanel>
            </Brand>
          </Link>
          <Humbarger onClick={() => setIsClicked(!isClicked)}>
            <HiBars3 />
          </Humbarger>
          <NavMenu clicked={isClicked.toString()} className="ms-auto">
            <Button
              to="/t/login"
              onClick={() => {
                setIsClicked(false);
              }}
            >
              Teacher Login
            </Button>
            {/* <Button
              to="/student-login"
              onClick={() => {
                setIsClicked(false);
              }}
            >
              Login
            </Button>
            <Button
              to="/student-signup"
              onClick={() => {
                setIsClicked(false);
              }}
            >
              Sing up
            </Button> */}
          </NavMenu>
        </Container>
      </Navbar>
    </Header>
  );
};

export default NavigationBar;
export const Header = styled.header``;

export const NavMenu = styled.div`
  display: flex;
  flex-wrap: nowrap;
  white-space: none;
  gap: 0 0.4rem;
  transition: all ease 0.8s;

  @media (max-width: 992px) {
    position: absolute;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 58px);
    top: 58px;
    left: ${(props) => (props.clicked === "true" ? "0px" : "-3000px")};
    bottom: 0;
    gap: 0.5rem 0;
    background: rgba(19, 15, 64, 1);
    align-items: center;
    padding-top: 2rem;
    transition: all ease 0.8s;
  }
`;

export const Link = styled(NavLink)`
  text-decoration: none;
  color: #fff;
  font-size: 1rem;
  font-weight: 500;
  padding: 0.4rem 1rem;

  &:hover {
    &:nth-child(1) {
      border: none;
      color: #fff;
    }
    border: 1px dashed rgba(256, 256, 256, 0.3);
    border-radius: 5px;
  }
`;

export const Image = styled.img`
  width: 40px;
  height: 40px;
  padding: 0.2rem 0.3rem;
  border-radius: 5px;
  background: rgba(241, 242, 246, 1);
`;
export const Brand = styled.div`
  display: flex;
  flex-wrap: nowrap;
  white-space: nowrap;
  gap: 0 0.4rem;
  align-items: center;
`;
export const BrandPanel = styled.div`
  display: flex;
  flex-direction: column;
`;

export const BrandText = styled.h1`
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 0;
`;

export const Humbarger = styled.div`
  width: 38px;
  height: 38px;
  border: 1px solid rgba(256, 256, 256, 0.5);
  color: #fff;
  font-size: 1.7rem;
  display: none;
  align-items: center;
  justify-content: center;
  border-radius: 3px;
  margin-left: auto;
  cursor: pointer;
  @media (max-width: 992px) {
    display: flex;
  }
`;

export const Button = styled(Link)`
  color: #fff;
  border-radius: 25px;
  &:hover {
    background: rgba(256, 256, 256, 0.2);
    font-weight: 500;
  }
`;
