import { createSlice } from "@reduxjs/toolkit";

const ViewSlice = createSlice({
  name: "viewInfo",
  initialState: {
    data: [],
    flag: false,
    id: null,
  },
  reducers: {
    setViewData: (state, action) => {
      state.id = action.payload.id;
      state.flag = true;
    },
    reset: (state) => {
      state.data = [];
      state.id = null;
      state.flag = false;
    },
  },
});

export const { setViewData, reset } = ViewSlice.actions;

export default ViewSlice.reducer;
