import { createSlice } from "@reduxjs/toolkit";

const ReloadSlicer = createSlice({
  name: "reload",
  initialState: {
    flag: false,
  },
  reducers: {
    setReload: (state) => {
      state.flag = true;
    },
    reset: (state) => {
      state.flag = false;
    },
  },
});

export const { setReload, reset } = ReloadSlicer.actions;

export default ReloadSlicer.reducer;
